html * {
  font-family: "DM Sans", "Helvetica Neue", "Poppins", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Arial", "sans-serif";
}

.theme-button-text {
  font-family: "DM Sans", "Helvetica Neue", "Poppins", "-apple-system", "BlinkMacSystemFont", "Segoe UI", "Roboto", "Arial", "sans-serif";
  font-size: 14px;
  margin: 5px 0;
  /* Make sure the text is aligned to the SVG */
  vertical-align: middle;
}

.blue {
  font-size: 250%;
  margin-top: 0;
  margin-bottom: 0;
}

.mini-blue {
  font-size: 170%;
  margin-top: 0;
  font-weight: bold;
  margin-bottom: 0;
}

.section-header {
  margin: 0;
  font-size: 150%;
}

.left-align {
  text-align: left;
}

.right-align {
  text-align: right;
}

@media (max-width: 767px) {
  .float-left {
    text-align: center;
  }

  .float-right {
    text-align: center;
  }
  .float-clear {
    text-align: center;
  }
}

@media (min-width: 768px) {
  .float-left {
    float: left;
  }

  .float-right {
    float: right;
  }
  .float-clear {
    clear: both;
  }
}

@media (max-width: 300px) {
  .big-time-since,
  .big-time-till {
    font-size: 400%;
    font-weight: bold;
    margin: 0;
    padding: 0;
    margin-block: 0;
    margin-inline: 0;
    text-align: center !important;
  }
}

@media (min-width: 241px) and (max-width: 767px) {
  .big-time-since,
  .big-time-till {
    font-size: 700%;
    font-weight: bold;
    margin: 0;
    padding: 0;
    margin-block: 0;
    margin-inline: 0;
    text-align: center !important;
  }
}

@media (min-width: 768px) and (max-width: 1400px) {
  .big-time-since,
  .big-time-till {
    font-size: 1000%;
    font-weight: bold;
    margin: 0;
    padding: 0;
    margin-block: 0;
    margin-inline: 0;
    text-align: center !important;
  }
}

@media (min-width: 1401px) {
  .big-time-since,
  .big-time-till {
    font-size: 1200%;
    font-weight: bold;
    margin: 0;
    padding: 0;
    margin-block: 0;
    margin-inline: 0;
  }
}

@media (max-width: 767px) {
  .small-time-since,
  .small-time-till {
    font-size: 200%;
    margin: 0;
    text-align: center !important;
  }
}

@media (min-width: 768px) {
  .small-time-since,
  .small-time-till {
    font-size: 200%;
    margin: 0;
  }
}

.button {
  margin: 15px;
}